.container {
  width: auto;
}

.container .box {
  background-color: #c4c4c4;
  border-radius: 10px;
  width: 400px;
  text-align: center;
}

.form-input {
  margin: 15 15;
  background-color: #ebe2e2;
  border: 0px;
  height: 40px;
  min-width: -webkit-fill-available;
}

.form-input[type=text] {
  text-indent: 15px;
}

.btn {
  color: white;
  width: 100px;
  height: 40px;
  border: 0px;
  margin-top: 20px;
  margin-bottom: 80px;
  margin-right: 10px;
  margin-left: 10px;
}

.danger {
  background: #b03333;
  border-color: #b03333;
}

.success {
  background: #0e722a;
  border-color: #0e722a;
}

.ball {
  background-color: red;
  border: 0px;
  border-radius: 55px;
  width: 100px !important;
  height: 100px;
  display: inline-block;
  margin-top: 35px !important;
  margin-bottom: 15px !important;
}

.center {
  margin: auto;
  width: 50%;
}

.input-group {
  text-align: center;
  width: 65%;
  margin: auto;
}

.custom-radio {
  display: inline-flex;
  cursor: pointer;
  position: relative;
}

.custom-radio input[type=radio] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  appearance: none;
}

.custom-radio .blue label:before {
  content: " ";
  display: inline-block;
  position: relative;
  top: 5px;
  margin: 0 5px 0 0;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  border: 1px solid #3196cf;
  background-color: #3196cf;
}

.custom-radio .red label:before {
  content: " ";
  display: inline-block;
  position: relative;
  top: 5px;
  margin: 0 5px 0 0;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  border: 1px solid #b03333;
  background-color: #b03333;
}

.custom-radio .blue input[type=radio]:checked + label:after {
  border-radius: 10px;
  width: 10px;
  height: 10px;
  position: absolute;
  right: 7.8rem;
  top: 0.7rem;
  content: " ";
  display: block;
  background: white;
}

.custom-radio .red input[type=radio]:checked + label:after {
  border-radius: 10px;
  width: 10px;
  height: 10px;
  position: absolute;
  right: 3.4rem;
  top: 0.7rem;
  content: " ";
  display: block;
  background: white;
}

.checkbox {
  display: block;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin: 10 10;
}

.checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checksymbol {
  position: absolute;
  left: 50px;
  height: 20px;
  width: 20px;
  background-color: #b03333;
}

.checkbox:hover input ~ .checksymbol {
  background-color: #b03333;
}

.checkbox input:checked ~ .checksymbol {
  background-color: #b03333;
}

.checksymbol:after {
  content: "";
  position: absolute;
  display: none;
}

.checkbox input:checked ~ .checksymbol:after {
  display: block;
}

.checkbox .checksymbol:after {
  left: 6px;
  top: 2px;
  width: 5px;
  height: 9px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}